import { useState } from "react";
import { motion } from "framer-motion";
import NavMenu from "./NavMenu";

export default function Header() {
  const [menuOpen, setMenuOpen] = useState(false);

  const toggleMenu = () => setMenuOpen((prevState) => !prevState);

  return (
    <>
      {!menuOpen ? (
        <motion.header 
          className="fixed top-0 left-0 right-0 z-50 bg-white/95 backdrop-blur-sm border-b border-gray-100"
          initial={{ y: -100, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ duration: 0.8, ease: "easeOut" }}
        >
          <div className="max-w-6xl mx-auto px-6 py-4">
            <div className="flex justify-between items-center">
              {/* Logo */}
              <motion.div 
                className="text-xl font-bold text-gray-900 tracking-wider"
                initial={{ opacity: 0, x: -20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ duration: 0.5, delay: 0.2 }}
              >
                JULIUS GUEVARRA
              </motion.div>

              {/* Navigation */}
              <nav className="hidden md:flex space-x-8">
                {["About", "Works", "Contact", "Blogs"].map((item, index) => (
                  <motion.a 
                    key={item}
                    href={`#${item.toLowerCase()}`} 
                    className="text-gray-700 hover:text-gray-900 transition-colors duration-200 relative group text-sm tracking-wider"
                    initial={{ opacity: 0, y: -10 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.5, delay: 0.3 + index * 0.1 }}
                    whileHover={{ y: -2 }}
                  >
                    {item}
                    <motion.div
                      className="absolute -bottom-1 left-0 w-0 h-0.5 bg-gray-900 group-hover:w-full transition-all duration-300"
                      initial={{ width: 0 }}
                      whileHover={{ width: "100%" }}
                    />
                  </motion.a>
                ))}
              </nav>

              {/* Menu Button */}
              <motion.button
                onClick={toggleMenu}
                className="md:hidden flex flex-col space-y-1 p-2 group"
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
                initial={{ opacity: 0, x: 20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ duration: 0.5, delay: 0.4 }}
              >
                <motion.span 
                  className="w-6 h-0.5 bg-gray-900 transition-all duration-300"
                  animate={{ rotate: menuOpen ? 45 : 0, y: menuOpen ? 6 : 0 }}
                />
                <motion.span 
                  className="w-6 h-0.5 bg-gray-900 transition-all duration-300"
                  animate={{ opacity: menuOpen ? 0 : 1 }}
                />
                <motion.span 
                  className="w-6 h-0.5 bg-gray-900 transition-all duration-300"
                  animate={{ rotate: menuOpen ? -45 : 0, y: menuOpen ? -6 : 0 }}
                />
              </motion.button>
            </div>
          </div>
        </motion.header>
      ) : (
        <NavMenu toggleMenu={toggleMenu} menuOpen={menuOpen} />
      )}
    </>
  );
}
